<template>
	<div id="orderList">
		<vue-better-scroll
		    style="height: 87vh"
		    class="wrapper"
		    ref="scroll"
		    :scrollbar="scrollbarObj"
		    :pullDownRefresh="pullDownRefreshObj"
		    :pullUpLoad="pullUpLoadObj"
		    :startY="parseInt(startY)"
		    @pullingDown="onPullingDown"
		    @pullingUp="onPullingUp">
		<ul :style="backgroundImg1">
      <div class="tipsNull" v-show="nullStatus">当前暂无订单哦</div>
			<li v-show="!nullStatus" v-for="(item, index) in orderList" v-bind:key="index">
			    <div class="grayLine"></div>
	       	    <div class="content">
	       	   		<p class="listTop fix">
	       	   			<span class="line"></span>
		       	   		<span class="listNum">订单编号：{{item.billCode}}</span>
		       	   		<span v-if="item.status==0" class="ltStatus ltStatusWait">{{item.status | typeFilter}}</span>
		       	   		<span v-else-if="item.status==1" class="ltStatus ltStatusSuccess">{{item.status | typeFilter}}</span>
		       	   		<span v-else class="ltStatus ltStatusfail">{{item.status | typeFilter}}</span>
		       	    </p>
		       	    <div class="ltDetail">
		       	   		<p>商品名称：挂号费</p>
		       	   		<p>订单金额：{{item.fee | moneyFilter}}</p>
		       	   		<p>下单时间：{{item.createTime }}</p>
		       	    </div>
		       	    <div class="ltBtns" v-if="item.status==0">
		       	    	<p class="timeGone" v-show="item.status == 0">剩余时间{{ item.tjsp }}</p>
		       	   		<span class="yesBtn" @click="okBtn(item.billCode)">立即支付</span>
		       	   		<span class="noBtn" @click="cancelBtn(item.billCode)">取消订单</span>
		       	    </div>
	       	    </div>
			</li>
		</ul>
		</vue-better-scroll>
    </div>
</template>

<script>
import { apiUrl } from "@/common/js/api";
import { ajaxPost } from "@/common/js/public";
import { Dialog } from "vant";
import VueBetterScroll from "vue2-better-scroll";
import store from "@/vuex/index";
import moment from "moment"; 
let count = 1;
let timers;
export default {
  name: "user",
  data() {
    return {
      nullStatus: false,
      orderList: [],
      scrollbarObj: false,
      backgroundImg1: { background: "none" },
      pullDownRefreshObj: {
        threshold: 90,
        stop: 40
      },
      // 这个配置用于做上拉加载功能，默认为 false。当设置为 true 或者是一个 Object 的时候，可以开启上拉加载，可以配置离底部距离阈值（threshold）来决定开始加载的时机
      pullUpLoadObj: {
        threshold: 0,
        txt: {
          more: "加载更多",
          noMore: "没有更多数据了"
        }
      },
      startY: 0, // 纵轴方向初始化位置
      scrollToX: 0,
      scrollToY: 0,
      scrollToTime: 700,
      items: []
    };
  },
  components: {
    VueBetterScroll
  },
  created() {
    this.getOrder();
  },
  updated() {
    clearInterval(timers);
    this.startTimer();
  },
  mounted() {
    clearInterval(timers);
    this.startTimer();
  },
  methods: {
    startTimer() {
      var that = this;
      timers = setInterval(() => {
        for (var key in this.orderList) {
          if (this.orderList[key]["status"] == 0) {
            var m1 =
              moment(
                moment(this.orderList[key]["createTime"]).format(
                  "YYYY-MM-DD HH:mm:ss"
                )
              ) + 300000; //后台时间
            var m2 = moment(moment().format("YYYY-MM-DD HH:mm:ss")); //当前时间
            var du = moment.duration(m1 - m2); //moment 格式化时间相减
            //console.log(du._milliseconds + " 23232");
            var mins = du.get("minutes");
            var ss = du.get("seconds");
            if (du._milliseconds / 1000 > 0) {
            } else {
              mins = 0;
              ss = 0;
              that.getOrder(); //该行自动刷新
              clearInterval(timers);
            }
            function toTwoNum(a) {
              return a < 10 ? "0" + a : a;
            }
            this.orderList[key]["tjsp"] = toTwoNum(mins) + ":" + toTwoNum(ss);
          }
        }
      }, 1000);
    },
    okBtn(b) {
      //去支付
      let that = this;
      store.commit("wmodule/changeBillCode", b);//修改订单号
      /* ajaxPost( apiUrl.getBillWithSignDetByBillCode, {
        billCode: b,
      }, (data) => {
        console.log(data.data.body);
        var data = data.data.body; */
        data = {
          resultCode: 1000,
          data: {
            doctorSignTime: {
              isExpert: false
            },
            deptId: 1024,
            deptName: '内科测试',
            doctorName: '黎明',
            doctId: 54124,
            doctorSignTime: {
              createTime: 1559701555832,
              startTime: 1559701555832,
              endTime: 1559701555838,
              signCode: 51641,
              weekDay: 4
            },
            fee: 100,
            hospitalName: '四医院测试',
            hospId: 651,
          }
        }
        if(data.resultCode == 1000){
            //curAppointmentWeek
            //console.log("yaoayaoaoaao "+ moment(data.data.doctorSignTime.createTime).format('YYYY-MM-DD'))
            console.log('data.data.doctorSignTime.isExpert',data.data.doctorSignTime.isExpert);
            store.commit("wmodule/changeDept", {deptId: data.data.deptId,deptName: data.data.deptName});//修改科室id 科室名称
            store.commit("wmodule/changeDoctor",{doctorId: data.data.doctId,doctorName:  data.data.doctName,doctorLevel: data.data.doctorSignTime.isExpert } );//修改医生id 名称 医生level
            store.commit("wmodule/changeAppointmentDate", {appointDate: moment(data.data.doctorSignTime.createTime).format('YYYY-MM-DD'),signStartTime:  data.data.doctorSignTime.startTime,signEndTime:  data.data.doctorSignTime.endTime, id:  data.data.doctorSignTime.id,doctScheduleId:  data.data.doctorSignTime.doctScheduleId,signCode: data.data.doctorSignTime.signCode,appointWorkDay: data.data.doctorSignTime.weekDay});//修改预约时间  开始和结束时间 1 2 修改预约号码appointmentNumId 医生排班的id 改号 改week
            store.commit("wmodule/changeFee",  data.data.fee);//修改挂号费用
            store.commit("wmodule/changeHospital", {hospitalId:  data.data.hospId,hospitalName:  data.data.hospName});//修改医院id 和名称 
            that.$router.push({ name: "ackPay" });//跳转到去支付页面
        }
      /* },function(error){
        console.log(error);
      }) */ 
    },
    cancelBtn(a) {
      //取消订单
      var that = this;
      Dialog.confirm({
        title: "提示",
        message: '<div style="text-align: center;">确认取消此订单吗</div>'
      })
        .then(() => {
          //ajaxPost("http://192.168.50.128:7083/aidoct-portal/h5/bill/updateBillByBillCode",{
          ajaxPost(
            apiUrl.cancelOrederList,
            {
              status: "2",
              billCode: a //--手机号【必填】
            },
            function(data) {
              var data = data.data.body;
              if (data.resultCode == 1000) {
                that.getOrder();
              }
            }
          );
        })
        .catch(() => {});
    },
    getOrder() {
      var that = this;
      that.orderList = []; //重置数据
      count = 1;
      /* ajaxPost( apiUrl.getOrederList, {
        userId: JSON.parse(sessionStorage.getItem("userInfo")).userId,
        currPage: "1",
        pageSize: "10"
      }, data => {
        console.log(data);
        var data = data.data.body; */
        data = {
          resultCode: 1000,
          data: {

          }
        }
        if (data.resultCode == 1000) {
          if (data.data.length == 0) {
            that.nullStatus = true;
            that.backgroundImg1 = {
              background: "url(" + require("../../assets/orde_null.png") + ") #fff no-repeat center 2.6rem",
              backgroundSize: "4rem 2.8rem"
            };
          } else {
            that.nullStatus = false;
            that.backgroundImg1 = {
              background: "none"
            };
            that.orderList = data.data; //赋值
            for (var i = 0; i < that.orderList.length; i++) {
              that.orderList[i].createTime = moment(
                that.orderList[i].createTime
              ).format("YYYY-MM-DD HH:mm:ss");
            }
            that.orderList.map((obj, index) => {
              var m1 = moment(moment().format("YYYY-MM-DD HH:mm:ss")), //当前时间
                m2 =
                  moment(
                    moment(obj.createTime).format("YYYY-MM-DD HH:mm:ss")
                  ) + 300000, //后台传入时间
                du = moment.duration(m2 - m1),
                mins = du.get("minutes"),
                ss = du.get("seconds");
              console.log(du, "dududududd");
              function toTwoNum(a) {
                return a < 10 ? "0" + a : a;
              }
              var firstData = toTwoNum(mins) + ":" + toTwoNum(ss);
              if (du._milliseconds / 1000 < 0) {
                firstData = "";
              } else {
              }
              console.log(obj);
              if (obj.status == 0) {
                that.$set(obj, "tjsp", firstData);
              }
            });
            //console.log(that.orderList);
          }
          that.$refs.scroll.forceUpdate(true);
        }
      //});
    },
    // 滚动到页面顶部
    scrollTo() {
      this.$refs.scroll.scrollTo(
        this.scrollToX,
        this.scrollToY,
        this.scrollToTime
      );
    },
    /* getData () {
		        return new Promise(resolve => {
		          setTimeout(() => {
		            const arr = []
		            for (let i = 0; i < 2; i++) {
		              arr.push(count++)
		            }
		            resolve(arr)
		          }, 1000)
		        })
		    },*/
    onPullingDown() {
      //下拉刷新
      this.getOrder();
    },
    onPullingUp() {
      // 模拟上拉 加载更多数据
      count++;
      var that = this;
      console.log(count);
      //ajaxPost("http://192.168.50.128:7083/aidoct-portal/h5/bill/getBillByUserIdLimit",{
      ajaxPost(
        apiUrl.getOrederList,
        {
          //'userId': JSON.parse(sessionStorage.getItem('userId')).phone,
          userId: JSON.parse(sessionStorage.getItem("userInfo")).userId,
          currPage: count.toString(),
          pageSize: "10"
        },
        function(data) {
          console.log(data);
          var data = data.data.body;
          if (data.resultCode == 1000) {
            if (count < data.page.totalPage + 1) {
              for (let i = 0; i < data.data.length; i++) {
                that.orderList.push(data.data[i]);
              }
              for (var i = 0; i < that.orderList.length; i++) {
                that.orderList[i].createTime = moment(
                  that.orderList[i].createTime
                ).format("YYYY-MM-DD HH:mm:ss");
              }
              that.orderList.map((obj, index) => {
                var m1 = moment(moment().format("YYYY-MM-DD HH:mm:ss")), //当前时间
                  m2 =
                    moment(
                      moment(obj.createTime).format("YYYY-MM-DD HH:mm:ss")
                    ) + 300000, //后台传入时间
                  du = moment.duration(m2 - m1),
                  mins = du.get("minutes"),
                  ss = du.get("seconds");
                function toTwoNum(a) {
                  return a < 10 ? "0" + a : a;
                }
                var firstData = toTwoNum(mins) + ":" + toTwoNum(ss);
                if (du._milliseconds / 1000 < 0) {
                  firstData = "";
                } else {
                }
                if (obj.status == 0) {
                  that.$set(obj, "tjsp", firstData);
                }
              });
              that.$refs.scroll.forceUpdate(true);
            } else {
              that.$refs.scroll.forceUpdate(false);
            }
          }
        }
      );
      /*ajaxPost(apiUrl.getOrederList,{
                    'userId': JSON.parse(sessionStorage.getItem('userId')).phone  //--手机号【必填】
                }, function(data) {
                    var data = data.data.body;
                    that.orderList = data.name;
                })*/
    }
  },
  filters: {
    typeFilter: function(myInput) {
      var result;
      if (myInput == 0) {
        result = "待支付";
      } else if (myInput == 1) {
        result = "已支付";
      } else {
        result = "已失效";
      }
      return result;
    },
    moneyFilter: function(myInput) {
      return myInput / 100 + "元";
    }
  },
  computed: {
    timesGo(myInput) {
      var m1 = moment(moment().format("YYYY-MM-DD HH:mm:ss")), //当前时间
        m2 = moment(moment(myInput).format("YYYY-MM-DD HH:mm:ss")), //后台传入时间
        du = moment.duration(m2 - m1),
        mins = du.get("minutes"),
        ss = du.get("seconds");
      function toTwoNum(a) {
        return a < 10 ? "0" + a : a;
      }
      var auth_time = du._milliseconds / 1000;
      console.log(auth_time);
      var auth_timetimer = setInterval(() => {
        auth_time--;
        m1 = moment(moment().format("YYYY-MM-DD HH:mm:ss")); //当前时间
        du = moment.duration(m2 - m1);
        //console.log(du + "du");
        if (auth_time <= 0) {
          clearInterval(auth_timetimer);
        }
      }, 3000);
      console.log(m1);
      return toTwoNum(mins) + ":" + toTwoNum(ss);
    }
  }
};
</script>

<style scoped lang="less">
.fix {
  zoom: 1;
}
.fix:after {
  content: "";
  clear: both;
  display: block;
  visibility: hidden;
  height: 0;
}
#orderList {
  width: 100%;
  height: calc(100% + 10px);
  background-color: #f6f7f8;
  ul {
    min-height: 87vh;
    .tipsNull {
      font-size: 0.14 * 2rem;
      text-align: center;
      padding-top: 3.2 * 2rem;
      font-family: PingFangSC-Regular;
    }
    li {
      font-size: 0.14 * 2rem;
      .grayLine {
        width: 100%;
        height: 10px;
      }
      .content {
        background-color: #fff;
        padding: 0 0.15 * 2rem;
        .listTop {
          width: 100%;
          height: 0.14 * 2rem;
          font-size: 0.14 * 2rem;
          line-height: 0.14 * 2rem;
          padding: 0.17 * 2rem 0 0.14 * 2rem 0;
          border-bottom: 1px solid #f6f7f8;
          .line {
            width: 0.03 * 2rem;
            height: 0.14 * 2rem;
            float: left;
            background-color: #3391e8;
          }
          .listNum {
            color: #272727;
            margin-left: 0.1 * 2rem;
          }
          .ltStatus {
            float: right;
          }
          .ltStatusWait {
            color: #ff941f;
          }
          .ltStatusSuccess {
            color: #3391e8;
          }
          .ltStatusfail {
            color: #7d7d7d;
          }
        }
        .ltDetail {
          padding: 0.14 * 2rem 0 0.06 * 2rem 0;
          border-bottom: 1px solid #f6f7f8;
          p {
            height: 0.2 * 2rem;
            line-height: 0.2 * 2rem;
            opacity: 0.6;
            margin-bottom: 0.06 * 2rem;
            padding-left: 0.1 * 2rem;
          }
        }
        .ltBtns {
          position: relative;
          width: 100%;
          height: 0.58 * 2rem;
          span {
            line-height: normal;
            padding: 0.1rem 0.32rem;
            box-sizing: border-box;
            text-align: center;
            display: block;
            float: left;
            border-radius: 2px;
          }
          .noBtn {
            border: 1px solid #b1afaf;
            color: #272727;
            float: right;
            margin-top: 0.14 * 2rem;
            margin-right: 0.19 * 2rem;
          }
          .yesBtn {
            border: 1px solid #3391e8;
            color: #fff;
            float: right;
            background-color: #3391e8;
            margin-top: 0.14 * 2rem;
          }
          .timeGone {
            color: #f97d66;
            background: url("../../assets/order_icon_time.png") no-repeat left
              center;
            box-sizing: border-box;
            display: table-cell;
            vertical-align: middle;
            padding: 0.13rem 0;
            text-indent: 0.5rem;
            line-height: normal;
            background-size: 0.3rem 0.3rem;
            float: left;
            margin-top: 0.3rem;
            margin-left: 0.26rem;
          }
        }
      }
    }
  }
}
</style>


